import React, { Component } from 'react';
import Header from '../component/header';
import Footer from '../component/footer';
import Product from '../component/product'
import Sidebar from '../component/sidebar';
import SortBy from '../component/sortBy';
import './index.css';

class Home extends Component {

  constructor (props){
    super(props);
    let products = [
      {id: 1, name: "product A", price: '$10', shipping: 'free shipping', url:"https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQ5azB_EG6ZqYNHMh6_iBmEo2rc3vczNMfxHlAOGi5bXA2LkAFYbg", size: 'S'},
      {id: 2, name: "product B", price: '$100', shipping: 'free shipping', url:"https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRZT-t_9ZmRG3QcGdUCMyF9AMdBUykODDxhTjBbd2J_Dt3OpT_hFw", size: 'L'},
      {id: 3, name: "product C", price: '$101', shipping: ' ', url:"https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcS11K-fYDqFWT3n2mrMnDQfHHDD69yQKLhKOnP9xY6O1numY25qAA", size: 'M'},
      {id: 4, name: "product D", price: '$102', shipping: 'free shipping', url:"data:image/jpeg;base64,", size: 'M'},
      {id: 5, name: "product E", price: '$104', shipping: ' ', url:"https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRth_snzfZIyh-KSjbcU8Y9F8v6DHMyGkuTsAqzLFwto-gKC4Ea6A", size: 'L'},
      {id: 6, name: "product F", price: '$104', shipping: 'free shipping', url:"https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRo2HpgXQnRxIt1s6HTdEV8W15EdFp67oNidUxlsZHydoYUgY_MBg", size: 'XXL'},
      {id: 7, name: "product G", price: '$102', shipping: ' ', url:"data:image/jpeg;base64,", size: 'XL'},
      {id: 8, name: "product H", price: '$108', shipping: 'free shipping', url:"https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQ4jhYVUp5wIVaLtxqVkvGqerYGkjkfDQ3MXik_WJluAb1Tc8tG", size: 'S'}
    ];
    this.state = {
      products : products,
      sizes: [
        {id: 1, size: 'S'},
        {id: 2, size: 'M'},
        {id: 3, size: 'L'},
        {id: 4, size: 'XL'},
        {id: 5, size: 'XXL'},
      ],
      sortby: [
        {id: 1, option: 'high to low', name: 'High to Low'},
        {id: 2, option: 'low to hign', name: 'Low to High'},
      ],
      filteredArray: products,
    }
  }
  
  sizeFilter = (size) => {
    this.setState({
      filteredArray: this.state.products.filter(item =>(
        item.size === size
      ))
    })
  }

  clearFilter = () =>{
    this.setState({
      filteredArray: this.state.products
    })
  }

  afterSetStateFinished() {
    this.setState({
      filteredArray: this.state.selectedValue === "high to low" ? this.state.filteredArray.sort((a,b) => (a.price < b.price) ? 1 : ((b.price < a.price) ? -1 : 0)): this.state.filteredArray.sort((a,b) => (a.price > b.price) ? 1 : ((b.price > a.price) ? -1 : 0))
    })
    console.log(this.state.filteredArray,'#################')
  }

  handleChange = (event) => {
    this.setState({
      selectedValue: event.target.value
    }, () => {
        this.afterSetStateFinished();
    });
  }

  render() {
    return (
      <div className="App">
        <Header/>
        <div className="content-wrapper">
          <div className="sidebarWrapper">
            <h4>Sizes</h4>
            <button className="clearBtn" onClick={this.clearFilter}>Clear Filter</button>
            <Sidebar handleChange={this.handleChange} sizeFilter={this.sizeFilter} size={this.state.sizes}/>
          </div>
          <div className="selectBoxWrapper">
            <span>Order By</span>
            <select className="selectboxStyle" value={this.state.sortby.option} onChange={this.handleChange}>
                <SortBy selectedValue={this.state.sortby} />
            </select>
          </div>
          <div className="listwrap">
            <span className="productCount">{this.state.filteredArray.length} product(s) found</span>
            <ul className="">
              <Product product={this.state.filteredArray}/>
            </ul>
          </div>
        </div>
        <Footer/>
      </div>
    );
  }
}

export default Home;
